"use client";
import React, { useEffect, useRef } from "react";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import SkincareLanding from "./skincare-landing";
import Ecomlanding from "./ecommerce-landing";
import ResLanding from "./reslanding";
gsap.registerPlugin(ScrollTrigger);

const slides = [
  {
    id: 1,
    bgColor: "bg-indigo-900",
    title: "Creative Portfolio",
    subtitle: "Web Developer & UI/UX Designer",
    description: "Creating digital experiences that connect and inspire",
  },
  {
    id: 2,
    bgColor: "bg-purple-900",
    title: "Featured Projects",
    subtitle: "Innovative Solutions",
    description: "Discover my latest work and creative process",
  },
  {
    id: 3,
    bgColor: "bg-blue-900",
    title: "Technical Skills",
    subtitle: "Expert Technologies",
    description: "Frontend, backend, and everything in between",
  },
  {
    id: 4,
    bgColor: "bg-pink-900",
    title: "Let's Connect",
    subtitle: "Work Together",
    description: "Ready to bring your vision to life",
  },
];

const AnimatedSlider = () => {
  const containerRef = useRef(null);
  const slidesRef = useRef([]);

  useEffect(() => {
    if (!containerRef.current) return;
    const totalSlides = slides.length;

    // Set widths dynamically
    gsap.set(slidesRef.current, { width: "100vw", height: "100vh" });

    const tl = gsap.timeline({
      scrollTrigger: {
        trigger: containerRef.current,
        pin: true,
        scrub: 0.8,
        snap: {
          snapTo: 1 / (totalSlides - 1),
          duration: { min: 0.1, max: 0.3 },
          delay: 0,
          ease: "power1.inOut",
          inertia: false,
        },
        end: () => `+=${window.innerHeight * (totalSlides - 1)}`,
      },
    });

    // Animate horizontal scroll
    tl.to(slidesRef.current, {
      xPercent: -100 * (totalSlides - 1),
      ease: "none",
    });

    // Animate content inside each slide
    slidesRef.current.forEach((slide, index) => {
      const content = slide.querySelector(".slide-content");
      gsap.fromTo(
        content,
        { opacity: 0, y: 50 },
        {
          opacity: 1,
          y: 0,
          duration: 0,
          scrollTrigger: {
            trigger: slide,
            containerAnimation: tl,
            start: "left center",
            toggleActions: "play none none reverse",
          },
        }
      );
    });

    return () => {
      ScrollTrigger.getAll().forEach((t) => t.kill());
    };
  }, []);

  return (
    <div
      ref={containerRef}
      className="relative w-screen h-[800px] overflow-hidden"
    >
      <div className="flex w-max h-full">
        {slides.map((slide, index) => (
          <section key={slide.id} ref={(el) => (slidesRef.current[index] = el)}>
            <div className="slide-content text-white w-full h-full opacity-0">
              {index === 0 && <SkincareLanding />}
              {index === 1 && <Ecomlanding />}
              {index === 2 && <ResLanding />}
              {index === 3 && (
                <div className="flex flex-col justify-center items-center h-full">
                  <h2 className="text-4xl font-bold">{slide.title}</h2>
                  <h3 className="text-2xl mt-2">{slide.subtitle}</h3>
                  <p className="mt-4">{slide.description}</p>
                </div>
              )}
            </div>
          </section>
        ))}
      </div>
    </div>
  );
};

export default AnimatedSlider;
